<template>
    <div>
        <StandardHeader :search="search"></StandardHeader>
    </div>
</template>

<script>
// Library components.
// Custom components.
import StandardHeader from './StandardHeader.vue'
import router from '../router'

export default {
    name: 'Home',
    components: {
        StandardHeader
    },

    data() {
        return {
            loading: false
        }
    },
    created: function() {
        //this.search()
    },
   
    methods: {
        search(srhtext) {
            //router.push({ name: 'vendorsearch', params: { srhtext: 'srhtext' }})
            router.push({ path: `/vendorsearch/${srhtext}` }) // -> /user/123
            //link vendor search page.
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



