<template>
    <div>
        <StandardHeader :search="search"></StandardHeader>
        <VendorSearchResult :searchdata="vendersearchresult"></VendorSearchResult>
    </div>
</template>

<script>
// Library components.
import axios from 'axios';
// Custom components.
import VendorSearchResult from './VendorSearchResult.vue'
import StandardHeader from './StandardHeader.vue'

export default {
    name: 'VendorSearch',
    components: {
        VendorSearchResult,
        StandardHeader
    },
    data() {
        return {
            username: 'saritasa@fooduniverse.com',
            password: 'Apple2018!',
            endpoint: 'https://api.fooduniverse.com/service/api/data/doquery',
            loading: false,
            token: '',
            vendersearchresult: [],
            navkey1: ''
        }
    },

    created: function() {
        this.search()
    },
    watch: {
        '$route': 'search'
    },
    methods: {
        search(ZSearchDesc1 = '') {
            //alert('search')
            //alert(this.$route.params.srhtext)
            if (ZSearchDesc1 == '')
                if (typeof (this.$route.params.srhtext) != "undefined")
                    ZSearchDesc1 = this.$route.params.srhtext
            //alert(ZSearchDesc1);
            //Get token.
            var config = {
                headers: {
                    'Content-Type': "application/x-www-form-urlencoded"
                }
            };

            this.loading = true

            //get Access token by username and password.
            var data_array = {
                grant_type: 'password',
                username: this.username,
                password: this.password
            };

            const data_string = Object.keys(data_array).map((key) => {
                return encodeURIComponent(key) + '=' + encodeURIComponent(data_array[key]);
            }).join('&');

            axios({
                method: 'post',
                url: 'http://ab.fooduniverse.com/service/token',
                data: data_string,
                config
            }).then(response => {

                // Set token by username and password.
                this.token = response.data.access_token

                config = {
                    headers: {
                        'Content-Type': "application/json",
                        'Authorization': "bearer" + this.token
                    }
                };
                // Get Vendor Information.
                axios.post(this.endpoint + '/VendorSearch-Vendors', {
                    ZSearchDesc1: ZSearchDesc1,
                    config
                }).then((response) => {
                    this.vendersearchresult = response.data.Items;
                    console.log(this.vendersearchresult)
                })
            })
        }

    }
}
</script>


