<template>
    <div>
        <div class="grid-container">
            <div class="grid-x grid-padding-x">
                <div class="cell small-12">
                    <nav aria-label="You are here:" role="navigation">
                        <ul class="breadcrumbs">
                            <li>
                                <span class="show-for-sr">Current: </span> Home
                            </li>
                        </ul>
                    </nav>
                </div>
            </div>
        </div>
        <div v-show="visible">
            <header class="grid-container">
                <div class="grid-x grid-padding-x">
                    <div class="cell small-12">
                        <div @click="countperpage=0">
                            <h2>All Vendors</h2>
                        </div>

                        <hr />
                    </div>
                </div>
            </header>
            <nav class="grid-container">
                <div class="grid-x grid-padding-x">
                    <div class="cell small-8 medium-5 xlarge-4">
                        <div class="filter-container">
                            <select>
                                <option></option>
                                <option>ABB Score</option>
                                <option>Featured</option>
                                <option>Star ratings</option>
                                <option>Price: low to high</option>
                                <option>Price: high to low</option>
                                <option>Best seller</option>
                                <option>Newest</option>
                            </select>

                        </div>
                    </div>
                    <div class="cell medium-4 xlarge-3 show-for-medium">
                        <div class="filter-container">
                            <select v-model="countperpage">
                                <option value="0">All</option>
                                <option value="4">4 per page</option>
                                <option value="24">24 per page</option>
                                <option value="48">48 per page</option>
                                <option value="96">96 per page</option>
                            </select>
                        </div>
                    </div>
                    <div class="cell small-4 medium-3 xlarge-2">
                        <button class="button filter-button" type="button" data-toggle="filter-off-canvas">
                            FILTER
                        </button>
                    </div>
                </div>
            </nav>
            <main id="search-results-grid">

                <div class="grid-container">
                    <div class="grid-x grid-padding-x">

                        <!--BEGIN LOOP of API ARRAY: VendorSearch-Vendors-->

                        <div class="cell small-12 smedium-6 medium-4 xlarge-3" v-for="vendor in itemsofcurpage" v-bind:key="vendor.navkey">
                            <router-link :to="'/vendor/'+vendor.navkey">
                                <a class="results-card" href="#">
                                    <!--Cui, is there a way to call the PartnerPage.html (you developed yesterday) without exposing the NavKey?-->

                                    <div class="grid-x grid-margin-x">
                                        <div class="card-img cell small-6 smedium-12 medium-12 align-self-top">
                                            <img :src="vendor.logoimagefilepath" :alt="vendor.desc1" />
                                            <button class="score-badge #VendorSearch-Vendors.ABBDesc1#">{{vendor.abbscore}}</button>
                                            <button class="button secondary button-add-cart show-for-smedium">ADD</button>
                                        </div>
                                        <div class="card-info cell small-6 smedium-12 medium-12">
                                            <div class="ratings">
                                                <div class="empty-stars"></div>
                                                <div class="full-stars" style="width:#VendorSearch-Vendors.UserRatingPerc#%;"></div>
                                            </div>
                                            <small class="subtitle">{{vendor.vendortitletext}}</small>
                                            <big class="product-name">
                                                <strong>{{vendor.desc1}}</strong>
                                            </big>
                                            <small class="product-units">{{vendor.city}}, {{vendor.statedesc1}}</small>
                                            <button class="button secondary hide-for-smedium">GO</button>
                                        </div>
                                    </div>
                                </a>
                            </router-link>
                        </div>

                        <!--END API ARRAY LOOP-->

                    </div>
                </div>
                <div class="grid-container">
                    <div class="search-pagination text-center">
                        <hr />
                        <div class="pagination" v-show="pagecount > 1">
                            <paginate :page-count="pagecount" :page-range="5" :prev-text="'Prev'" :next-text="'Next'" :container-class="'pagination'" :page-class="'page-item'" :click-handler="setpagenum">
                            </paginate>
                        </div>
                    </div>
                </div>
            </main>
        </div>
        <div v-show="searchdata.length==0">No result.</div>
    </div>
</template>

<script>
import Paginate from 'vuejs-paginate'

export default {
    name: 'VendorSearchResult',
    components: {
        Paginate
    },
    props: ["searchdata"],
    data() {
        return {
            currentpage: 1,
            countperpage: 24,
            pagecount: 0,
            visible: false
        }
    },
    computed: {
        // a computed getter
        itemsofcurpage: function() {
            if (this.searchdata.length > 0)
                this.visible = true
            if (this.countperpage == 0) {
                //if All is selected
                this.pagecount = 0;
                return this.searchdata;

            } else {
                this.pagecount = Math.round((this.searchdata.length + 1) / this.countperpage)
                return this.searchdata.slice((this.currentpage - 1) * this.countperpage, this.currentpage * this.countperpage)
            }

        }
    },
    methods: {
        setpagenum: function(pagenum) {
            this.currentpage = pagenum

        }
    }
}
</script>


